import React from 'react';
import { Switch, Route } from 'react-router-dom';
import ProductListContainer from './routes/ProductList/ProductListContainer';
import ProductPageContainer from './routes/ProductPage/ProductPageContainer';
import CartContainer from './routes/Cart/CartContainer';
import ErrorPage from './layout/error/ErrorPage';

const Router = props => (
  <Switch>
    <Route exact path="/" render={() => <div>HOMEPAGE</div>} />
    <Route exact path="/products" component={ProductListContainer} />
    <Route exact path="/products/:id" component={ProductPageContainer} {...props} />
    <Route exact path="/cart" component={CartContainer} />
    <Route path="/error" component={ErrorPage} />
    <Route component={ErrorPage} />
  </Switch>
);

export default Router;
