import { GET_PRODUCTS, INCREMENT_PAGE } from '../action-types/products';

const INIT = { settings: { page: 1, stopLoadMore: true }, items: [] };

export default function productsReducer(state = INIT, action) {
  const { type, payload } = action;

  switch (type) {
    case GET_PRODUCTS:
      return { ...state, items: [...state.items, ...payload.items], settings: { ...state.settings, stopLoadMore: payload.stopLoadMore } };
    case INCREMENT_PAGE:
      return { ...state, settings: { page: state.settings.page + 1 } };
    default:
      return state;
  }
}
