import React from 'react';
import { Link } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import { withStyles } from '@material-ui/core/styles';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ShoppingCart from '@material-ui/icons/ShoppingCart';

// including styles
import './ProductListItem.css';

const styles = {
  card: {
    minWidth: 350,
    margin: 20
  },
  media: {
    height: 300
  },
  content: {
    paddingBottom: 0,
    textAlign: 'center'
  },
  actions: {
    display: 'flex',
    justifyContent: 'center',
    paddingBottom: 16
  },
  cart: {
    marginLeft: 10
  },
  btn: {
    padding: '7px 10px'
  }
};

function ProductListItem(props) {
  const {
    classes, name, category, price, id, addToCart
  } = props;
  return (
    <Card className={classes.card}>
      <Link to={`/products/${id}`}>
        <CardMedia className={classes.media} image={`https://loremflickr.com/200/300/?${id}`} />
        <CardContent className={classes.content}>
          <Typography variant="headline">
            {name.length >= 20 ? `${name.slice(0, 20)}...` : name}
          </Typography>
          <Typography variant="caption">{category}</Typography>
          <Typography variant="subheading">{price.toFixed(2)}</Typography>
        </CardContent>
      </Link>

      <CardActions className={classes.actions}>
        <Button
          size="small"
          variant="contained"
          color="primary"
          className={classes.btn}
          onClick={addToCart}
        >
          Add to cart
          <ShoppingCart className={classes.cart} />
        </Button>
      </CardActions>
    </Card>
  );
}

export default withStyles(styles)(ProductListItem);
