import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import './style.css';
import { Button } from '@material-ui/core';
import ProductListItem from './ProductListItem';
import shape from '../../propTypesShape';

const styles = {
  btn: {
    width: 150
  }
};

class ProductList extends Component {
  static defaultProps = {
    products: []
  };

  static propTypes = {
    products: PropTypes.arrayOf(PropTypes.shape(shape))
  };

  componentDidMount() {
    const { incrementPage, fetchProducts, products } = this.props;
    if (products.length === 0) {
      fetchProducts();
      incrementPage();
    }
  }

  getMoreProducts = () => {
    const { incrementPage, fetchProducts } = this.props;
    fetchProducts();
    incrementPage();
  };

  render() {
    const { classes, addToCart, stopLoadMore } = this.props;
    return (
      <div className="product-list-wrapper">
        <div className="product-list">
          {this.props.products.map(product => (
            <ProductListItem {...product} key={product.id} addToCart={() => addToCart(product)} />
          ))}
        </div>
        <Button
          variant="contained"
          color="primary"
          className={classes.btn}
          onClick={this.getMoreProducts}
          disabled={stopLoadMore}
        >
          Show more
        </Button>
      </div>
    );
  }
}

export default withStyles(styles)(ProductList);
