import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import './style.css';

const Cart = ({ cart, totalPrice }) => (
  <div className="cart-table">
    <Typography component="h5" variant="display1" color="textSecondary" className="cart-header">
      YOUR CART
    </Typography>
    <Paper>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Product name</TableCell>
            <TableCell align="center">Category</TableCell>
            <TableCell align="right">Quantity</TableCell>
            <TableCell align="right">Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {cart.map((item) => {
            const {
              name, category, price, id, quantity
            } = item;
            return (
              <TableRow key={id}>
                <TableCell component="th" scope="row">
                  {name}
                </TableCell>
                <TableCell align="center">{category}</TableCell>
                <TableCell align="center">{quantity}</TableCell>
                <TableCell align="right">{(price * quantity).toFixed(2)}</TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </Paper>
    <Chip label={`Total price: ${totalPrice.toFixed(2)}`} color="primary" className="total-price" />
  </div>
);

export default Cart;
