import { CATCH_ERROR, CLEAR_ERROR } from '../action-types/error';

const INIT = '';

export default function errorReducer(state = INIT, action) {
  const { type, payload } = action;

  switch (type) {
    case CATCH_ERROR:
      return payload;
    case CLEAR_ERROR:
      return '';
    default:
      return state;
  }
}
