import axios from 'axios';
import { GET_PRODUCTS, INCREMENT_PAGE } from '../action-types/products';
import { PRODUCT_LIMIT } from '../constants/productConstants';

const getItemsWithFlag = payload => ({ type: GET_PRODUCTS, payload: { items: payload.items, stopLoadMore: payload.stopLoadMore } });

export const fetchProducts = () => (dispatch, getState) => {
  const { page } = getState().products.settings;
  axios.get(`http://localhost:3000/api/products?_page=${page}&_limit=${PRODUCT_LIMIT}`).then((result) => {
    if (Number(result.headers['x-total-count']) - getState().products.items.length < PRODUCT_LIMIT) {
      dispatch(getItemsWithFlag({ items: result.data, stopLoadMore: true }));
    } else {
      dispatch(getItemsWithFlag({ items: result.data, stopLoadMore: false }));
    }
  });
};

export const incrementPage = () => ({ type: INCREMENT_PAGE });
