import axios from 'axios';
import { FETCH_PRODUCT } from '../action-types/products';
import { catchError } from './error';

export const fetchProductCreator = payload => ({ type: FETCH_PRODUCT, payload });

export const fetchProduct = id => (dispatch) => {
  let resStatus;
  axios.get(`http://localhost:3000/api/products/${id}`)
    .then((response) => {
      resStatus = response.status;
      return response.data;
    })
    .then((data) => {
      if (resStatus >= 400) dispatch(catchError(resStatus));
      else dispatch(fetchProductCreator(data));
    })
    .catch(err => dispatch(catchError(err.message)));
};
