import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import ShoppingCart from '@material-ui/icons/ShoppingCart';
import Typography from '@material-ui/core/Typography';
import { CardActions } from '@material-ui/core';
import shape from '../../propTypesShape';

const styles = () => ({
  wrapper: {
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    padding: '0 50px'
  },
  card: {
    display: 'flex',
    justifyContent: 'space-between',
    maxWidth: 1000,
    margin: '0 auto'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    width: '62%',
    padding: 20
  },
  cover: {
    width: '38%'
  },
  description: {
    padding: 15,
    paddingTop: 0
  },
  cart: {
    marginLeft: 10
  },
  btn: {
    padding: '7px 10px'
  }
});

class ProductPage extends Component {
  static defaultProps = {
    product: {}
  };

  static propTypes = {
    product: PropTypes.shape(shape)
  };

  componentDidMount() {
    const { id } = this.props.match.params;
    if (Object.keys(this.props.product).length === 0) this.props.fetchProduct(id);
  }

  render() {
    const { classes, product, addToCart } = this.props;
    return (
      <div className={classes.wrapper}>
        <Card className={classes.card}>
          <div className={classes.details}>
            <CardContent className={classes.content}>
              <Typography component="h5" variant="h5">
                {product.name}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                {product.category}
              </Typography>
              <Typography variant="h6" color="primary">
                Price:&nbsp;
                {product.price}
              </Typography>
            </CardContent>
            <Typography variant="body2" color="textPrimary" className={classes.description}>
              {product.description}
            </Typography>
            <CardActions>
              <Button
                size="small"
                variant="contained"
                color="primary"
                className={classes.btn}
                onClick={() => addToCart(product)}
              >
                Add to cart
                <ShoppingCart className={classes.cart} />
              </Button>
            </CardActions>
          </div>
          <CardMedia
            className={classes.cover}
            image={`https://loremflickr.com/500/300/?${product.id}`}
            title="Product image"
          />
        </Card>
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(ProductPage);
