import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import './error.css';

class Error extends Component {
  componentWillUnmount() {
    this.props.clearError();
  }

  render() {
    if (this.props.error) {
      return <Redirect to="/error" />;
    }

    return null;
  }
}

export default Error;
